.right-content-box {
  padding: 25px 45px;
  box-sizing: border-box;
}
.right-content-box .tables-box .info-box {
  margin-bottom: 15px;
}
.right-content-box .tables-box .info-box .title {
  color: #283e53;
  font-size: 16px;
}
.right-content-box .tables-box .info-box .tips {
  color: #6c849c;
  font-size: 14px;
}
.right-content-box .tables-box .time-tables {
  background: #f3fafb;
  border-color: #d4e1e9;
  border: 1px solid #d4e1e9;
}
.right-content-box .tables-box .time-tables .th-time {
  width: 11%;
}
.right-content-box .tables-box .time-tables th {
  height: 35px;
  color: #fff;
  background: #2ca6e0;
}
.right-content-box .tables-box .time-tables th,
.right-content-box .tables-box .time-tables td {
  text-align: center;
  max-width: 170px;
}
.right-content-box .tables-box .time-tables td {
  height: 50px;
  padding: 10px 20px;
  box-sizing: border-box;
  position: relative;
}
.right-content-box .tables-box .time-tables td span {
  display: block;
}
.right-content-box .tables-box .time-tables td span.txt {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
}
.right-content-box .tables-box .time-tables td.dis {
  background: #ececec;
}
.right-content-box .tables-box .time-tables td.dis span {
  color: #6c849c;
}
.right-content-box .tables-box .time-tables td.cancel-order {
  background: #84b3fa;
}
.right-content-box .tables-box .time-tables td.have-time {
  background: #7ecef4;
}
.right-content-box .tables-box .time-tables td.order {
  background: #0eb5f8;
}
.right-content-box .tables-box .time-tables td .tips-box {
  width: 260px;
  position: absolute;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 4px;
  top: 0;
  z-index: 999;
  text-align: left;
}
.right-content-box .tables-box .time-tables td .tips-box span {
  display: inline-block;
}
.right-content-box .tables-box .time-tables td .tips-box .tips-box-top .name {
  color: #283e53;
  margin-right: 15px;
}
.right-content-box .tables-box .time-tables td .tips-box .tips-box-top .inps {
  border: 1px solid #3598db;
  color: #3598db;
  box-shadow: none;
}
.right-content-box .tables-box .time-tables td .tips-box .tips-box-content .skillbar {
  width: 160px;
  height: 6px;
  border-radius: 4px;
  background: #eff4f7;
  margin-right: 18px;
}
.right-content-box .tables-box .time-tables td .tips-box .tips-box-content .skillbar .skill {
  width: 80%;
  height: 100%;
  border-radius: 4px;
  background: #0eb5f8;
}
.right-content-box .tables-box .time-tables .order-btn {
  color: #0082fe;
}
.right-content-box .tables-box .time-tables .order-btn:hover {
  text-decoration: underline;
}
.right-content-box .tables-box .time-tables .nothing {
  width: 22px;
  height: 2px;
  background: #d4e1e9;
  display: inline-block;
}
.right-content-box .tables-box .btn-box {
  margin-top: 60px;
  text-align: center;
}
.right-content-box .tables-box .btn-box .btns {
  margin: 0 10px;
}
